<template>
  <div class="project">
    <div class="project-title">
      <h2 class="text-center">Quests</h2>
    </div>
    <div class="project-content">
      <ul>
        <li v-for="(quest, project) in questStatus" :key="`quest-${project}`">
          <input type="checkbox" onclick="return false;" :checked="quest" />
          <span>{{questText[project]}}</span>
        </li>
      </ul>
    </div>
    <div class="project-close">
      <v-btn
        @click="$root.$emit('closeProject')"
        block large elevation="1" color="purple lighten-4">
        Close
      </v-btn>
    </div>
  </div>
</template>

<script>

export default {
  props: ['questStatus'],
  data: () => ({
    questText: {
      aloucast: 'Listen to the Radio',
      rickroll: 'Watch the Television',
      bakingrelay: 'Talk to the Chef Aloupeep',
      bdayvoices: 'Aloupeeps on the right has things to say',
      gsacover: 'Talk to Nina',
      bdaydance: 'Left Aloupeep with fireworks',
      mural: 'Check the painting',
      messages: 'Read the message cards',
      gallery: 'Check the gift pile',
      credits: 'Aloupeep with sunglasses',
      cake: '??? Bonus ????',
    },
  }),
  mounted() {
    // Count remaining
    const remain = Object.entries(this.questStatus)
      .filter(([project, status]) => !status && project !== 'cake')
      .length;
    // If no more remaining, unlock the cake
    if (!remain) {
      this.questText.cake = 'Blow the candles on the cake';
      this.$root.$emit('cakeUnlocked');
    }
  },
};
</script>

